/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

img {
	max-width: 100%;
	object-fit: cover;
}

.text-image-container {
	.editor-post-featured-image__preview,
	.editor-post-featured-image__toggle {
		height: 100%;
		max-height: none;
		position: relative;
	}

	.row.image-text { flex-direction: row-reverse; }
	.row.content-row { align-items: center; }

	h2 { font-family: 'Newsreader'; font-weight: 300; font-size: 3.5rem; margin: 0; margin-bottom: 0.25em; }
}

.text-image-container-large {
	.img-wrap { background: #F9E3EC; display: flex; justify-content: center; align-items: center; padding: 5rem; }
	.content-wrap { max-width: 500px; margin: auto; }
}

.text-image-container-featured {
	.img-wrap { background: var(--peach); padding: 5rem; }
	.content-wrap { max-width: 500px; margin: auto; }
}

.img-holder { position: relative; }
.video-btn { position: absolute; top: calc(50% - 2rem); left: calc(50% - 2rem); width: 4rem; height: 4rem; font-size: 1.5rem; background: var(--purple); color: #fff; display: flex; align-items: center; justify-content: center; border-radius: 50%; }


.editor-post-featured-image__preview .media-wrap { width: 100%; height: 100%; position: relative; }
.editor-post-featured-image__preview .select-image-overlay { position: absolute; width: 100%; background: rgb(0 0 0 / 20%); left: 0; top: 0; color: #fff; display: flex; justify-content: center; align-items: center; opacity: 0; transition: opacity 0.3s; height: 100%; }
.editor-post-featured-image__preview .pseudo-btn { border: 1px solid #fff; padding: 0.75rem 1rem; border-radius: 4px; }
.editor-post-featured-image__preview:hover .select-image-overlay { opacity: 1; }